// hooks/useTextColorFromBgImage.js
import { useEffect, useState } from "react";

export default function useTextColorFromBgImage(imageUrl) {
  const [textColor, setTextColor] = useState("black");

  useEffect(() => {
    if (!imageUrl) return;

    const img = new Image();
    img.crossOrigin = "anonymous"; // Required if image is from a different domain
    img.src = imageUrl;

    img.onload = () => {
      const canvas = document.createElement("canvas");
      const ctx = canvas.getContext("2d");

      canvas.width = img.width;
      canvas.height = img.height;
      ctx.drawImage(img, 0, 0);

      const imageData = ctx.getImageData(0, 0, canvas.width, canvas.height);
      const data = imageData.data;

      let r = 0,
        g = 0,
        b = 0;

      for (let i = 0; i < data.length; i += 4) {
        r += data[i];
        g += data[i + 1];
        b += data[i + 2];
      }

      const pixelCount = data.length / 4;
      r = r / pixelCount;
      g = g / pixelCount;
      b = b / pixelCount;

      const luminance = 0.299 * r + 0.587 * g + 0.114 * b;
      const finalTextColor = luminance > 186 ? "black" : "white";
      setTextColor(finalTextColor);
    };

    img.onerror = () => {
      setTextColor("black"); // fallback
    };
  }, [imageUrl]);

  return textColor;
}
